
/* eslint-disable no-unused-vars */
let me
let vm
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
name: 'my',
data () {
return {
nickName: '七灵',
count: 0
}
},
components: {Grid, GridItem, Group, Cell},
beforeMount () {
me = window.me
},
mounted () {

this.count = this.$store.state.cart.count
var canvas = document.getElementById('canvas')
var ctx = canvas.getContext('2d')
canvas.width = canvas.parentNode.offsetWidth
canvas.height = canvas.parentNode.offsetHeight

window.requestAnimFrame = (function () {
return window.requestAnimationFrame ||
window.webkitRequestAnimationFrame ||
window.mozRequestAnimationFrame ||
function (callback) {
window.setTimeout(callback, 1000 / 60)
}
})()

var step = 0

var lines = ['rgba(0,222,255, 0.2)',
'rgba(157,192,249, 0.2)',
'rgba(0,168,255, 0.2)']
var loop = function () {
ctx.clearRect(0, 0, canvas.width, canvas.height)
step++

for (var j = lines.length - 1; j >= 0; j--) {
ctx.fillStyle = lines[j]

var angle = (step + j * 45) * Math.PI / 180
var deltaHeight = Math.sin(angle) * 50
var deltaHeightRight = Math.cos(angle) * 50
ctx.beginPath()
ctx.moveTo(0, canvas.height / 2 + deltaHeight)
ctx.bezierCurveTo(canvas.width / 2, canvas.height / 2 + deltaHeight - 50, canvas.width / 2, canvas.height / 2 + deltaHeightRight - 50, canvas.width, canvas.height / 2 + deltaHeightRight)
ctx.lineTo(canvas.width, canvas.height)
ctx.lineTo(0, canvas.height)
ctx.lineTo(0, canvas.height / 2 + deltaHeight)
ctx.closePath()
ctx.fill()
}
window.requestAnimFrame(loop)
}
loop()
},
watch: {
'$route' (to, from) {
this.count = this.$store.state.cart.count
}
},
computed: {},
methods: {

setPageStatus (data) {
this.$emit('listenPage', data)
},
jumpTo (pathName, param, type) {
/* [type=2] 1:'path'2:'name',3:'query' */
type = type || 'name'
if (pathName) {
if (type === 1) {
this.$router.push({path: '/' + pathName + (param ? '/' + param : '')})
}
if (type === 2) {
this.$router.push({name: pathName, params: param || ''})
}
if (type === 3) {
this.$router.push({path: '/' + pathName, query: param || ''})
}
}
}
}
}



