
/* eslint-disable */

let vm
let me
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
name: 'auth',
data() {
return {
appId: 'wxa92cd39da6d03d8f',
appSecret: 'b6540309f545879b1b33fc8b95fc4f70',
}
},
created() {
me = window.me
},
components: {Tabbar, TabbarItem},
beforeMount() {

},
mounted() {

vm = this
let urlParam = me.getURLParams()

if (!me.locals.get('ynWxUser')) {
if (me.isWeixin) {


if (urlParam.code) {
window.location.href = 'https://api.weixin.qq.com/sns/oauth2/access_token?appid=' + vm.appId + '&secret=' + vm.appSecret + '&code=' + urlParam.code + '&grant_type=authorization_code'
} else {
window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + vm.appId + '&redirect_uri=' + window.encodeURIComponent('https://qinhua.github.io/youni-mall/dist/#/author') + '&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
}
} else {

}
} else {

this.login()
}
},
computed: {},
methods: {

getPageStatus(data) {
vm.curSelected = data
},
login() {
let url = this.webUrl + '/Wap/User/info'

this.$http.get(url).then(response => {
response = response.body
console.log(response)
if (response) {

this.$store.commit('getUser', response)
setTimeout(() => {
this.goBeforeLoginUrl() // 页面恢复(进入用户一开始请求的页面)
}, 2000)
} else {
this.alert('服务器tm撸猫去了 :(', 'wrong')
if (me.locals.get('ynWxUser')) {

window.location.href = this.webUrl + '/Wap/User/getOpenid'
}
}
})
},
goBeforeLoginUrl() {
let url = me.locals.get('beforeLoginUrl')
if (!url || url.indexOf('/author') !== -1) {
this.$router.push({path: '/home'})
} else {
this.$router.push({path: url})
me.locals.set('beforeLoginUrl', '')
}
}
},
watch: {

}
}



