
/* eslint-disable no-unused-vars */
let me
let vm
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import {userApi} from '../../service/main.js'

export default {
name: 'my-edit',
data () {
return {
onFetching: false,
isPosting: false,
userId: null,
nickName: ''
}
},
components: {Group, Cell, XInput},
beforeMount () {
me = window.me
},
mounted () {
vm = this

vm.userId = vm.$route.query.userId
console.log(vm.userId)
},
methods: {
updateUser () {
if (vm.isPosting) return false
if (!vm.nickName) {
vm.toast('请先填写！')
return false
}
vm.isPosting = true
vm.processing()
vm.loadData(userApi.updateName, {userId: vm.userId, nickName: vm.nickName}, 'POST', function (res) {
console.log(res, '修改用户信息')
this.$store.commit('updateNickName', vm.nickName)
vm.$router.back()
vm.isPosting = false
vm.processing(0, 1)
}, function () {
vm.isPosting = false
vm.processing(0, 1)
})
}
}
}



