
/* eslint-disable */
let me
let vm
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import {orderApi} from '../../service/main.js'

export default {
name: 'order',
data() {
return {
demo2: [{key: '1', value: 'A'},{key: '2', value: 'B'}],
checklist0011: [],
show: false,
curOrderFilter: '',
orders: [],
params: {
type: 0,
pagerSize: 10,
pageNo: 1,
goodsType: 'XXX',
goodsCategory: '',
brandId: '',
filter: ''
},
isPosting: false,
onFetching: false,
labelPosition: '',
error: '',
commonList: ['全选']
}
},
components: {Tab, TabItem, Checklist, XButton, Checker, CheckerItem},
beforeMount() {
me = window.me
},
mounted() {
vm = this
vm.getOrders()
vm.$nextTick(() => {
vm.$refs.orderScroller.finishInfinite(true)
vm.$refs.orderScroller.resize()
})
},
computed: {},
watch: {
'$route'(to, from) {
vm.getOrders()
}
},
methods: {

setPageStatus(data) {
this.$emit('listenPage', data)
},
toAppraise(id) {
this.$router.push({path: '/appraise' + (param ? '/' + param : '')})
},
refresh(done) {
console.log('下拉加载')
setTimeout(function () {
vm.getOrders()
vm.$refs.orderScroller.finishPullToRefresh()
}, 1200)
},
infinite(done) {
console.log('无限滚动')
setTimeout(function () {
vm.getOrders(true)
vm.$refs.orderScroller.finishInfinite(true)
}, 1000)
},
onItemClick(type) {
if (type === 'undefined') {
vm.params.type = ''
} else {
vm.params.type = type
}
vm.getOrders()
},
filterTicket(type, isMine) {
vm.curTicketFilter = type
vm.getOrders()
},
getOrders(isLoadMore) {
vm.params.type = vm.$route.params.id
if (vm.onFetching) return false
vm.processing()
vm.onFetching = true
vm.loadData(orderApi.orders, vm.params, 'POST', function (res) {
var resD = res.data.itemList
for (var i = 0; i < resD.length; i++) {
switch (resD[i].status) {
case -1:
resD[i].statusName = '已取消'
break
case 0:
resD[i].statusName = '待支付'
break
case 1:
resD[i].statusName = '待派送'
break
case 2:
resD[i].statusName = '派送中'
break
case 3:
resD[i].statusName = '待评价'
break
case 4:
resD[i].statusName = '已完成'
break
}
}
if (!isLoadMore) {
vm.orders = resD
} else {
vm.orders.push(resD)
}
console.log(vm.orders, '订单数据')
vm.onFetching = false
vm.processing(0, 1)
}, function () {
vm.onFetching = false
vm.processing(0, 1)
})
},
delOrder(id) {
if (vm.isPosting) return false
vm.confirm('确认删除？', '订单删除后不可恢复！', function () {
vm.isPosting = true
vm.loadData(orderApi.delOrder + '?id=' + id, vm.params, 'POST', function (res) {
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}, function () {
})
},
cancelOrder(id) {
if (vm.isPosting) return false
vm.confirm('确认取消？', '订单取消后不可恢复！', function () {
vm.isPosting = true
vm.loadData(orderApi.cancelOrder + '?id=' + id, vm.params, 'POST', function (res) {
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}, function () {

})
},
pushOrder(id) {
if (vm.isPosting) return false
vm.confirm('确认催单？', '请不要频繁催单！', function () {
vm.isPosting = true
vm.loadData(orderApi.cancelOrder + '?id=' + id, vm.params, 'POST', function (res) {
vm.isPosting = false
}, function () {
vm.isPosting = false
})
}, function () {

})
},
payOrder(id) {
if (vm.isPosting) return false
vm.isPosting = true
vm.loadData(orderApi.payOrder + '?id=' + id, vm.params, 'POST', function (res) {
vm.isPosting = false
}, function () {
vm.isPosting = false
})
},
change(val) {
console.log('change', val)
},
curCheck(itemValue, itemDisabled) {
console.log('checkbox', itemValue)
}
}
}



