
/* eslint-disable */
let me
let vm
import Swiper from '../components/Swiper'
import Group from 'vux/src/components/group/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import {homeApi} from '../service/main.js'
import {mapState, mapMutations} from 'vuex'

export default {
name: 'home',
data() {
return {
location: '',
banner: [],
notice: [],
goods: [],
/* filter start */
filterOffset: 0,
filters: {
goods: [
{
key: 0,
value: '全部'
},
{
key: 1,
value: '瓶装水'
},
{
key: 2,
value: '桶装水'
},
{
key: 3,
value: '牛奶'
},
{
key: -1,
value: '其它'
}
],
brands: [
{
key: 0,
value: '全部'
},
{
key: 1,
value: '怡宝'
},
{
key: 2,
value: '康师傅'
},
{
key: 3,
value: '百岁山'
},
{
key: 4,
value: '花果山'
},
{
key: 5,
value: '水老官'
},
{
key: 6,
value: '一方人'
},
{
key: 7,
value: '农夫山泉'
},
{
key: 8,
value: '八宝山'
},
{
key: 9,
value: '昆仑山'
}
],
specials: [
{
key: 0,
value: '全部'
},
{
key: 1,
value: '有优惠'
},
{
key: 2,
value: '有红包'
},
{
key: 3,
value: '买二送一'
}
]
},
curFilterType: '',
currentFilter: null,
filterData: [],
showFilterCon: false,
factive: '',
subActive: 0,
/* filter end */
showList: true,
scrollTop: 0,
onFetching: false,
isPosting: false,
pulldownConfig: {
content: '下拉刷新',
height: 60,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '松开以加载',
loadingContent: '加载中…',
clsPrefix: 'xs-plugin-pulldown-'
},
pullupConfig: {
content: '上滑加载更多',
height: 40,
pullUpHeight: 60,
autoRefresh: false,
downContent: '上滑加载',
upContent: '上滑加载',
loadingContent: '加载中…',
clsPrefix: 'xs-plugin-pullup-'
},
count: 0,
balls: [ //小球 设为3个
{
show: false
}, {
show: false
}, {
show: false
},
],
dropBalls: []
}
},
components: {
Swiper,
Group,
GroupTitle,
Grid,
GridItem,
Marquee,
MarqueeItem,
XNumber,
Scroller,
LoadMore
},
beforeMount() {
me = window.me
},
mounted() {
vm = this

this.count = this.$store.state.cart.count
vm.getPos()
vm.getBanner()
vm.getNotice()
vm.getGoods()

document.addEventListener('click', (e) => {
if (e.target.offsetParent) {
if (JSON.stringify(e.target.offsetParent.classList).indexOf('filter') === -1) {
vm.hideFilter()
return false
}
}
}, false)
vm.$nextTick(function () {
setTimeout(() => {
vm.filterOffset = vm.$refs.filters01.offsetTop
}, 500) //获取筛选栏位置
vm.$refs.myScroll.reset()
vm.$refs.myScroll.donePullup()
vm.$refs.myScroll.donePulldown()
})
},
computed: mapState({
curCount: state => state.cart.count
}),
watch: {
'$route'(to, from) {
vm.getPos()
}
},
methods: {

getPos() {
var lp = me.locals.get('cur5656Position')
setTimeout(function () {
if (lp) {
vm.location = JSON.parse(lp).name || JSON.parse(lp).formattedAddress
} else {
try {
vm.location = '定位中…'
var map, geolocation;

map = new AMap.Map('mapContainer', {
resizeEnable: true
});
map.plugin('AMap.Geolocation', function () {
geolocation = new AMap.Geolocation({
enableHighAccuracy: true,//是否使用高精度定位，默认:true
timeout: 10000,          //超过10秒后停止定位，默认：无穷大
buttonOffset: new AMap.Pixel(10, 20),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
zoomToAccuracy: true,  //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
buttonPosition: 'RB'
});
map.addControl(geolocation);
geolocation.getCurrentPosition();
AMap.event.addListener(geolocation, 'complete', onComplete);//返回定位信息
AMap.event.addListener(geolocation, 'error', onError);      //返回定位出错信息
});


function onComplete(data) {
me.locals.set('cur5656Position', JSON.stringify(data))
vm.location = data.formattedAddress
var str = ['定位成功'];
str.push('经度：' + data.position.getLng());
str.push('纬度：' + data.position.getLat());
if (data.accuracy) {
str.push('精度：' + data.accuracy + ' 米');
}

str.push('是否经过偏移：' + (data.isConverted ? '是' : '否'));

}


function onError(data) {
vm.location = '定位失败'

}
} catch (e) {
console.log(e)
}
}
}, 200)
},

setPageStatus(data) {
this.$emit('listenPage', data)
},
scrollHandler() {

setTimeout(function () {
let scrollTop = vm.$refs.home.scrollTop
let target = vm.$refs.filters01
let list = vm.$refs.goodsList
if (vm.showFilterCon) {
vm.hideFilter()
}
if (scrollTop >= vm.filterOffset) {
target.classList.add('fixed')
list.classList.add('fixed')
} else {
target.classList.remove('fixed')
list.classList.remove('fixed')
}
}, 300)
},
toMap() {
vm.$router.push({name: 'map', params: {path: vm.$route.path.replace(/\//g, '_')}})
},
toTopic(url) {
if (vm.showFilterCon) return
location.href = url
},
toDetail(id) {
if (vm.showFilterCon) return
vm.$router.push({name: 'goods_detail', query: {id: id}})
},
/* 页面数据 */
getBanner(cb) {
vm.loadData(homeApi.banner, null, 'POST', function (res) {
console.log(res.data, '首页Banner')
vm.banner = res.data.itemList
cb ? cb() : null
})
},
getNotice() {
vm.loadData(homeApi.topNotice, null, 'POST', function (res) {
console.log(res.data, '首页TopNews')
vm.notice = res.data.itemList
})
},
getGoods(isLoadMore) {
if (vm.onFetching) return false
var params = vm.filterData || {
pagerSize: 10,
pageNo: 1,
goodsType: 'XXX',
goodsCategory: '',
brandId: '',
filter: ''
}
vm.loadData(homeApi.goodsList, params, 'POST', function (res) {
console.log(res.data, '首页GoodsList')
if (!isLoadMore) {
vm.goods = res.data.itemList
if (res.data.noMore) {
vm.$nextTick(function () {
vm.$refs.myScroll.disablePullup()
})
}
} else {
vm.goods.push(res.data.itemList)
}
vm.onFetching = false
}, function () {
vm.onFetching = false
})
},
/* 商品筛选 */
showFilter(type, e) {
vm.factive = type
console.log(vm.subActive)
if (vm.showFilterCon) {
if (vm.curFilterType === type) {
vm.factive = ''
vm.showFilterCon = false
} else {
vm.curFilterType = type
vm.currentFilter = vm.filters[type]
vm.showFilterCon = true
}
} else {
vm.curFilterType = type
vm.currentFilter = vm.filters[type]
vm.showFilterCon = true
}

},
hideFilter() {
if (vm.showFilterCon) {
vm.showFilterCon = false
vm.factive = ''
}
},
chooseFilter(idx, key, value, e) {
vm.goods = []
console.log(JSON.stringify(vm.filterData), vm.curFilterType)
if (JSON.stringify(vm.filterData).indexOf(vm.curFilterType) === -1) {
vm.filterData.push({
type: vm.curFilterType,
filterId: key,
filterName: value !== '全部' ? value : ''
})
} else {
for (var i = 0; i < vm.filterData.length; i++) {
console.log(vm.filterData[i].filterName, value)
if (vm.filterData[i].filterName !== value) {
vm.filterData[i] = {
type: vm.curFilterType,
filterId: key,
filterName: value !== '全部' ? value : ''
}
}
}
}
vm.factive = ''
vm.showFilterCon = false
console.log(vm.filterData, '最后的筛选数据')
var lastF = {
goodsType: 1,
goodsCategory: 'water',
brandId: 2,
filter: '有折扣，有满减'
}
vm.getGoods(lastF)
},
/* 上下拉刷新 */
onPullDown() {
if (vm.onFetching) {

return false
} else {

setTimeout(function () {
vm.getGoods()
vm.$nextTick(function () {
vm.$refs.myScroll.reset({top: 0})
vm.$refs.myScroll.donePullup()
vm.$refs.myScroll.donePulldown()
})
}, 1500)
}
},
onPullUp() {
if (vm.onFetching) {

return false
} else {

setTimeout(function () {
vm.getGoods(true)
vm.$nextTick(function () {
vm.$refs.myScroll.reset({bottom: 0})
vm.$refs.myScroll.donePullup()
vm.$refs.myScroll.donePulldown()
})
}, 200)
}
},
onScroll(pos) {
this.scrollTop = pos.top
vm.factive = ''
vm.showFilterCon ? vm.showFilterCon = false : null
},
changeCount(obj) {
console.log(obj)
if (obj.type === 'add') {
this.additem(obj.event)
this.count++
} else if (obj.type === 'sub') {
this.count--
} else {
this.count = obj.value
}
vm.$store.commit('updateCart', this.count)
console.log(vm.$store.state.cart.count)
},
/* 购物车 */
additem(event) {
this.drop(event.target);
},
/* 购物车小球动画 */
drop(el) {

for (let i = 0; i < this.balls.length; i++) {
let ball = this.balls[i]
if (!ball.show) {
ball.show = true
ball.el = el
this.dropBalls.push(ball)
return;
}
}
},
beforeDrop(el) {
let count = this.balls.length
while (count--) {
let ball = this.balls[count]
if (ball.show) {
let rect = ball.el.getBoundingClientRect() //元素相对于视口的位置
let x = rect.left - 40
let y = -(window.innerHeight - rect.top - 22) //获取y
el.style.display = ''
el.style.webkitTransform = 'translateY(' + y + 'px)' //translateY
el.style.transform = 'translateY(' + y + 'px)'
let inner = el.getElementsByClassName('inner-hook')[0]
inner.style.webkitTransform = 'translateX(' + x + 'px)'
inner.style.transform = 'translateX(' + x + 'px)'
}
}
},
/*重置小球数量  样式重置*/
dropping(el, done) {
let rf = el.offsetHeight
let cartCls = vm.$refs.floatCart.classList
el.style.webkitTransform = 'translate3d(0,0,0)'
el.style.transform = 'translate3d(0,0,0)'
let inner = el.getElementsByClassName('inner-hook')[0]
inner.style.webkitTransform = 'translate3d(-10px,-80px,0)'
inner.style.transform = 'translate3d(-10px,-80px,0)'
el.addEventListener('transitionend', done)
cartCls.toggle('bulbing')
setTimeout(() => {
cartCls.remove('bulbing')
}, 800)
},
/*初始化小球*/
afterDrop(el) {
let ball = this.dropBalls.shift()
if (ball) {
ball.show = false
el.style.display = 'none'
}
}
}
}



