
/* eslint-disable */
let me
let vm
import Swiper from '../components/Swiper'
import Group from 'vux/src/components/group/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import {homeApi, nearbyApi} from '../service/main.js'

export default {
name: 'nearby',
data() {
return {
location: '',
banner: [],
notice: [],
shops: [],
filters: {
shop: [
{
key: 0,
value: '全部'
},
{
key: 1,
value: '瓶装水'
},
{
key: 2,
value: '桶装水'
},
{
key: 3,
value: '认证卖家'
},
{
key: 4,
value: '金牌卖家'
}
],
sort: [
{
key: 0,
value: '默认排序'
},
{
key: 1,
value: '离我最近'
},
{
key: 2,
value: '销量最高'
},
{
key: 3,
value: '好评最多'
}
],
specials: [
{
key: 0,
value: '全部'
},
{
key: 1,
value: '水票'
},
{
key: 2,
value: '满减'
},
{
key: 3,
value: '折扣'
}
]
},
curFilterType: '',
currentFilter: null,
filterData: [],
showFilterCon: false,
factive: '',
subActive: 0,
/* filter end */
showList: true,
scrollTop: 0,
roundValue: 0,
onFetching: false,
isPosting: false,
pulldownConfig: {
content: '下拉刷新',
height: 60,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '松开以加载',
loadingContent: '加载中…',
clsPrefix: 'xs-plugin-pulldown-'
},
pullupConfig: {
content: '上滑加载更多',
height: 40,
pullUpHeight: 60,
autoRefresh: false,
downContent: '上滑加载',
upContent: '上滑加载',
loadingContent: '加载中…',
clsPrefix: 'xs-plugin-pullup-'
},
}
},
components: {
Swiper,
Group,
GroupTitle,
Grid,
GridItem,
Marquee,
MarqueeItem,
XNumber,
Scroller,
LoadMore
},
beforeMount() {
me = window.me
},
mounted() {
vm = this

vm.getPos()
vm.getBanner()
vm.getNotice()
vm.getShops()

document.addEventListener('click', (e) => {
if (e.target.offsetParent) {
if (JSON.stringify(e.target.offsetParent.classList).indexOf('filter') === -1) {
vm.hideFilter()
return false
}
}
}, false)
vm.$nextTick(function () {
setTimeout(() => {
vm.filterOffset = vm.$refs.filters02.offsetTop
}, 500) //获取筛选栏位置
vm.$refs.myScroll.reset()
vm.$refs.myScroll.donePullup()
vm.$refs.myScroll.donePulldown()
})
},
computed: {},
watch: {
'$route'(to, from) {
vm.getPos()
}
},
methods: {

getPos() {
var lp = me.locals.get('cur5656Position')
setTimeout(function () {
if (lp) {
vm.location = JSON.parse(lp).name || JSON.parse(lp).formattedAddress
} else {
try {
vm.location = '定位中…'
var map, geolocation;

map = new AMap.Map('mapContainer', {
resizeEnable: true
});
map.plugin('AMap.Geolocation', function () {
geolocation = new AMap.Geolocation({
enableHighAccuracy: true,//是否使用高精度定位，默认:true
timeout: 10000,          //超过10秒后停止定位，默认：无穷大
buttonOffset: new AMap.Pixel(10, 20),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
zoomToAccuracy: true,  //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
buttonPosition: 'RB'
});
map.addControl(geolocation);
geolocation.getCurrentPosition();
AMap.event.addListener(geolocation, 'complete', onComplete);//返回定位信息
AMap.event.addListener(geolocation, 'error', onError);      //返回定位出错信息
});


function onComplete(data) {
me.locals.set('cur5656Position', JSON.stringify(data))
vm.location = data.formattedAddress
var str = ['定位成功'];
str.push('经度：' + data.position.getLng());
str.push('纬度：' + data.position.getLat());
if (data.accuracy) {
str.push('精度：' + data.accuracy + ' 米');
}

str.push('是否经过偏移：' + (data.isConverted ? '是' : '否'));

}


function onError(data) {
vm.location = '定位失败'

}
} catch (e) {
console.log(e)
}
}
}, 200)
},

setPageStatus(data) {
this.$emit('listenPage', data)
},
scrollHandler() {

setTimeout(function () {
let scrollTop = vm.$refs.nearby.scrollTop
let target = vm.$refs.filters02
let list = vm.$refs.shopsList
if (vm.showFilterCon) {
vm.hideFilter()
}
if (scrollTop >= vm.filterOffset) {
target.classList.add('fixed')
list.classList.add('fixed')
} else {
target.classList.remove('fixed')
list.classList.remove('fixed')
}
}, 300)
},
toMap() {
vm.$router.push({name: 'map', params: {path: vm.$route.path.replace(/\//g, '_')}})
},
toTopic(url) {
if (vm.showFilterCon) return
location.href = url
},
toDetail(id) {
if (vm.showFilterCon) return
vm.$router.push({name: 'shops_detail', query: {id: id}})
},
preBook(id) {
vm.$router.push({path: '/detail/' + id})
},
/* 页面数据 */
getBanner(cb) {
vm.loadData(homeApi.banner, null, 'POST', function (res) {
console.log(res.data, '首页Banner')
vm.banner = res.data.itemList
cb ? cb() : null
})
},
getNotice() {
vm.loadData(homeApi.topNotice, null, 'POST', function (res) {
console.log(res.data, '首页TopNews')
vm.notice = res.data.itemList
})
},
getShops(isLoadMore) {
if (vm.onFetching) return false
var params = vm.filterData || {
pagerSize: 10,
pageNo: 1,
goodsType: 'XXX',
goodsCategory: '',
brandId: '',
filter: ''
}
vm.loadData(nearbyApi.shopsList, params, 'POST', function (res) {
console.log(res.data, '首页shopsList')
if (!isLoadMore) {
vm.shops = res.data.itemList
if (res.data.noMore) {
vm.$nextTick(function () {
vm.$refs.myScroll.disablePullup()
})
}
} else {
vm.shops.push(res.data.itemList)
}
vm.onFetching = false
}, function () {
vm.onFetching = false
})
},
/* 商品筛选 */
showFilter(type, e) {
vm.factive = type

if (vm.showFilterCon) {
if (vm.curFilterType === type) {
vm.factive = ''
vm.showFilterCon = false
} else {
vm.curFilterType = type
vm.currentFilter = vm.filters[type]
vm.showFilterCon = true
}
} else {
vm.curFilterType = type
vm.currentFilter = vm.filters[type]
vm.showFilterCon = true
}

},
hideFilter() {
if (vm.showFilterCon) {
vm.showFilterCon = false
vm.factive = ''
}
},
chooseFilter(idx, key, value, e) {
vm.shops = []
console.log(JSON.stringify(vm.filterData), vm.curFilterType)
if (JSON.stringify(vm.filterData).indexOf(vm.curFilterType) === -1) {
vm.filterData.push({
type: vm.curFilterType,
filterId: key,
filterName: value !== '全部' ? value : ''
})
} else {
for (var i = 0; i < vm.filterData.length; i++) {
console.log(vm.filterData[i].filterName, value)
if (vm.filterData[i].filterName !== value) {
vm.filterData[i] = {
type: vm.curFilterType,
filterId: key,
filterName: value !== '全部' ? value : ''
}
}
}
}
vm.factive = ''
vm.showFilterCon = false
console.log(vm.filterData, '最后的筛选数据')
var lastF = {
goodsType: 1,
goodsCategory: 'water',
brandId: 2,
filter: '有折扣，有满减'
}
vm.getShops(lastF)
},
onPullDown() {
if (vm.onFetching) {

return false
} else {

setTimeout(function () {

vm.getShops()
vm.$nextTick(function () {
vm.$refs.myScroll.reset({top: 0})
vm.$refs.myScroll.donePullup()
vm.$refs.myScroll.donePulldown()
})
}, 1500)
}
},
onPullUp() {
if (vm.onFetching) {

return false
} else {

setTimeout(function () {

vm.getShops(true)
vm.$nextTick(function () {
vm.$refs.myScroll.reset({bottom: 0})
vm.$refs.myScroll.donePullup()
vm.$refs.myScroll.donePulldown()
})
}, 200)
}
},
onScroll(pos) {
this.scrollTop = pos.top
vm.factive = ''
vm.showFilterCon ? vm.showFilterCon = false : null
}
}
}



