
/* eslint-disable */
let me
let vm
import Swiper from 'swiper'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Group from 'vux/src/components/group/index.vue'

import {goodsApi} from '../../service/main.js'

export default {
name: 'goods-detail',
data() {
return {
id: null,
show: false,
curOrderFilter: '',
imgs: [],
tablist: ['商品详情', '规格', '评论'],
balls: [ //小球 设为3个
{
show: false
}, {
show: false
}, {
show: false
},
],
dropBalls: [],
isPosting: false,
onFetching: false,
goodsNum: 0,
unitPrice: 10,
detailSwiper: null,
curIndex: 0,
appIdx: 0,
appraiseData: null,
appraise: []
}
},
components: {Tab, TabItem, XNumber, Group},
beforeMount() {
me = window.me
},
mounted() {
vm = this
vm.id = vm.$route.query.id

vm.getDetail(function () {
vm.mySwiper()
vm.swiperDetail()
vm.getAppraise()
})




},
computed: {
total() {
return vm.goodsNum * vm.unitPrice
}
},
watch: {
'$route'(to, from) {
vm.getDetail()
}
},
methods: {

setPageStatus(data) {
this.$emit('listenPage', data)
},
mySwiper() {
return new Swiper('.banner-goods-detail .swiper-container', {
initialSlide: 0,
direction: 'horizontal',
autoplay: 2000,
preloadImages: true,
autoplayDisableOnInteraction: false,
observer: true,
observeParents: true,

pagination: '.swiper-detail .swiper-pagination',
paginationClickable: true,



grabCursor: true,





})
},
swiperDetail() {
vm.detailSwiper = new Swiper('.swiper-container.swiper-goods-detail', {
initialSlide: 0,
direction: 'horizontal',
preloadImages: true,
autoplayDisableOnInteraction: false,
observer: true,
autoHeight: true,
observeParents: true,
grabCursor: true,



onSlideChangeEnd: function (swiper) {
vm.curIndex = swiper.activeIndex
}
})
},
addToCart(id) {
vm.goodsNum++
},
refresh(done) {
console.log('下拉加载')
setTimeout(function () {
vm.getOrders()
vm.$refs.orderScroller.finishPullToRefresh()
}, 1200)
},
infinite(done) {
console.log('无限滚动')
setTimeout(function () {
vm.getOrders(true)
vm.$refs.orderScroller.finishInfinite(true)
}, 1000)
},
filterTicket(type, isMine) {
vm.curTicketFilter = type
vm.getOrders()
},
getDetail(cb) {
vm.id = vm.$route.params.id
if (vm.onFetching) return false
vm.processing()
vm.onFetching = true
vm.loadData(goodsApi.detail, {id: vm.id}, 'POST', function (res) {

vm.imgs = res.data.itemList
console.log(vm.imgs, '商品图片数据')
cb ? cb() : null
vm.onFetching = false
vm.processing(0, 1)
}, function () {
vm.onFetching = false
vm.processing(0, 1)
})
},
chooseCol(index) {
vm.detailSwiper.slideTo(index)
},
getAppraise(index) {
vm.loadData(goodsApi.appraise, {type: index || 0}, 'POST', function (res) {
vm.appraiseData = vm.appraise = res.data.itemList
console.log(vm.appraiseData, '用户评价数据')
cb ? cb() : null
}, function () {
})
},
filterAppraise(index) {
vm.appIdx = index
vm.appraise = []
if (vm.appraiseData) {
if (index === 0) {
vm.appraise = vm.appraiseData
return;
}
for (let i = 0; i < vm.appraiseData.length; i++) {
if (vm.appraiseData[i].type === index) {
vm.appraise.push(vm.appraiseData[i])
}
}
} else {
vm.getAppraise()
}
},
changeNum(obj) {

console.log(arguments)
if (obj.type === 'add') {
this.additem(obj.event)
this.goodsNum++
} else if (obj.type === 'sub') {
this.goodsNum--
} else {
this.goodsNum = obj.value
}
vm.$store.commit('updateCart', this.goodsNum)
console.log(vm.$store.state.cart.count)
},
/* 购物车 */
additem(event) {
this.drop(event.target);
},
/* 购物车小球动画 */
drop(el) {

for (let i = 0; i < this.balls.length; i++) {
let ball = this.balls[i]
if (!ball.show) {
ball.show = true
ball.el = el
this.dropBalls.push(ball)
return;
}
}
},
beforeDrop(el) {
let count = this.balls.length
while (count--) {
let ball = this.balls[count]
if (ball.show) {
let rect = ball.el.getBoundingClientRect() //元素相对于视口的位置
let x = rect.left - 40
let y = -(window.innerHeight - rect.top - 22) //获取y
el.style.display = ''
el.style.webkitTransform = 'translateY(' + y + 'px)' //translateY
el.style.transform = 'translateY(' + y + 'px)'
let inner = el.getElementsByClassName('inner-hook')[0]
inner.style.webkitTransform = 'translateX(' + x + 'px)'
inner.style.transform = 'translateX(' + x + 'px)'
}
}
},
/*重置小球数量  样式重置*/
dropping(el, done) {
let rf = el.offsetHeight

el.style.webkitTransform = 'translate3d(0,0,0)'
el.style.transform = 'translate3d(0,0,0)'
let inner = el.getElementsByClassName('inner-hook')[0]
inner.style.webkitTransform = 'translate3d(0,-10px,0)'
inner.style.transform = 'translate3d(0,-10px,0)'
el.addEventListener('transitionend', done)
/* cartCls.toggle('bulbing')
setTimeout(() => {
cartCls.remove('bulbing')
}, 800) */
},
/*初始化小球*/
afterDrop(el) {
let ball = this.dropBalls.shift()
if (ball) {
ball.show = false
el.style.display = 'none'
}
}
}
}



